import { Component } from '@angular/core';
import { FormControl } from '@angular/forms';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'serivce-now-chrome-extension';
  active = 1;
  theGroups={};//Definition = {key:color}
  settings = {automateSNOW:true,activeExtension:true};
  theBlacklist = {"www":true};
  url_tester='';
  url_title='';
  url_error='';
  component_error = '';
  tempValue = '';
  chrome_colors = ["grey","blue","red","yellow","green","pink",'purple','cyan'];
  ngOnInit(): void {
    chrome.storage.sync.get(['theGroups','settings','theBlacklist'], (data) => {
      this.theGroups      = data.theGroups;
      this.settings       = data.settings;
      this.theBlacklist   = data.theBlacklist;
    });
  }

  public updateSettings(){if (this.settings){chrome.storage.sync.set({settings:this.settings});}}
  //public updateActive(){if (this.activeExtension){chrome.storage.sync.set({activeExtension:this.activeExtension});}}
  public updateColor(){if (this.theGroups){chrome.storage.sync.set({theGroups:this.theGroups});}}//end updateColor
  public addtheBlacklist(val){
    //console.log(val);
    if (val && !this.theBlacklist[val]){//index of is to prevent duplicates
      //console.log(val);
      this.theBlacklist[val] = true;
      chrome.storage.sync.set({theBlacklist:this.theBlacklist});
      if (val){delete this.theGroups[val];chrome.storage.sync.set({theGroups:this.theGroups});}
    }
  }
  public remtheBlacklist(val){
    if (val && this.theBlacklist[val]){
      delete this.theBlacklist[val];chrome.storage.sync.set({theBlacklist:this.theBlacklist});
    }
  }

  public urlTester(theEvent:any){
    var theTitle='';
    if (theEvent && (!theEvent.startsWith('http'))){this.url_error="Warning! URL must have https:// or http:// at the front";}
    if (theEvent && (theEvent.startsWith('http'))){
      this.url_error='';
      theTitle=theEvent.toString().split('/')[2].toString();
      if (theTitle.includes('.')){
        var tempURL = theTitle.split('.');
        tempURL.length = tempURL.length-1;//Cut off the .com, .org, etc.
        theTitle=tempURL.toString().replace(',','.');
      }
      this.url_title= theTitle;
    } else //Take the third object in the array. This should ALWAYS be the distinct URL. Exceptions are custome chrome URLS such as chrome://newtab/;}
    if (theTitle && (this.theBlacklist[theTitle])){
      this.url_error="DANGER! This item is on the theBlacklist! Please remove it from the theBlacklist before adding";
    }
  }
  public deleteKey(theKey){
    if (theKey){delete this.theGroups[theKey];chrome.storage.sync.set({theGroups:this.theGroups});}
  }
  public changeKey(theKey){
    if (this.tempValue && theKey){
      //Copy existing key to new value
      this.theGroups[this.tempValue] = this.theGroups[theKey].toString();
      //Delete the old
      delete this.theGroups[theKey];
      //Sync the new
      chrome.storage.sync.set({theGroups:this.theGroups});
    }
  }
  public addGroup(val){
    this.theGroups[val] = "grey";chrome.storage.sync.set({theGroups:this.theGroups});
  }
  public runDebug(){
    console.log(this);
  }
  public syncVars(){
    chrome.storage.sync.get(['theGroups','settings','theBlacklist'], (data) => {
      console.log(data);
      this.theGroups      = data.theGroups;
      this.settings       = data.settings;
      this.theBlacklist   = data.theBlacklist;
    });
  }
}
